.page {
  padding-top: 100px;

  &-header,
  &-title {
    margin-bottom: 96px;
  }

  &-header {
    .page-title {
      margin-bottom: 0;
    }
  }

  &-title {
    max-width: 1000px;
    line-height: 0.95;
  }
}

.page-header-news {
  overflow: hidden;

  .page-title {
    float: left;
    margin-right: 40px;
  }
  .page-header-btn {
    float: right;
    padding-top: 3em;
  }

  @media (max-width: 1439px) {
    .page-header-btn {
      padding-top: 2em;
    }
  }
  @media (max-width: 1259px) {
    .page-header-btn {
      padding-top: 1em;
    }
  }
}

.news-list {
  margin-bottom: 80px;
}
.news-item {
  display: flex;
  padding-bottom: 80px;
  margin-bottom: 80px;
  border-bottom: 2px solid rgba(#fff, 0.15);
  @include fz(22);
  color: #FFF;

  a {
    color: #FFF;
  }

  &-img {
    flex: 20.833%;
    max-width: 20.833%;
    line-height: 0;
    text-align: right;

    a img,
    img {
      display: inline-block;
      border-radius: 140px;
      transition: transform .3s;
      will-change: transform;
    }
  }
  &-body {
    padding-right: 10%;
    flex: 0 0 79.177%;
    max-width: 79.177%;
  }
  &-title {
    @include fz(54);
    line-height: 1;
    margin-bottom: 35px;
    transition: color .3s;

    a,
    span {
      display: inline;
      // position: relative;
    }

    &-arrow {
      // position: absolute;
      // right: -68px;
      // top: 50%;
      // margin-top: -21px;
      position: relative;
      top: -1px;
      display: inline;
      vertical-align: middle;
      width: 48px;
      height: 48px;
      stroke: $primary;
      stroke-width: 2;
      fill: none;
      opacity: 0;
      transform: translate3d(-10px, 0, 0);
      transition: transform 0.3s, opacity 0.3s;
      will-change: transform, opacity;
    }
  }
  &-txt {
    margin-bottom: 32px;
  }
  &-date {
    color: $primary;
    font-weight: 600;
  }

  &:hover {
    color: #FFF;

    a {
      color: $primary;
    }
    .news-item {
      &-title {
        color: $primary;

        &-arrow {
          opacity: 1;
          transform: translate3d(0, 0, 0);
        }
      }

      &-img {
        img {
          transform: scale(1.03);
        }
      }
    }
  }

  @media (max-width: 1439px) {
    @include fz(18);

    &-title {
      @include fz(42);
    }
  }

  @media (max-width: 1259px) {
    &-title {
      @include fz(32);
    }
  }
}


.ui-tags {
  @include fz(24);
  margin-bottom: 60px;

  &-list {
    li {
      margin-bottom: 24px;
    }
  }

  &-link {
    display: inline-block;
    vertical-align: middle;
    padding: 0px 20px;
    height: 60px;
    line-height: 58px;
    border: 1px dashed rgba(#fff, 0.15);
    // border: 1px solid transparent;
    border-radius: 30px;
    color: #fff;
    // background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='30' ry='30' stroke='white' stroke-width='2' stroke-dasharray='1%2c 10' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");

    &-dark {
      color: #000000;
      border-color: #000000;
    }
    &.active,
    &:hover {
      color: $primary;
      border-color: $primary;
      border-style: solid;
    }
  }

  &-more {
    vertical-align: middle;
    margin-left: 20px;
    border-bottom: 1px dashed rgba($primary, 0.8);

    &:hover {
      color: #fff;
      // border-bottom-style: solid;
      border-bottom-color: transparent;

      .ui-tags-more-arrow {
        stroke: #fff;
      }
    }

    &-arrow {
      position: relative;
      top: -1px;
      display: inline-block !important;
      vertical-align: middle;
      margin-left: 5px;
      width: 12px;
      height: 10px;
      fill: none;
      stroke: $primary;
      stroke-width: 2;
    }
  }

  @media (max-width: 1439px) {
    @include fz(20);

    &-link {
      height: 56px;
      line-height: 54px;
    }
  }

  @media (max-width: 1259px) {
    @include fz(14);

    &-list {
      li {
        display: inline-block;
        margin-bottom: 15px;
        margin-right: 15px;
      }
    }
    &-link {
      height: 48px;
      line-height: 46px;
    }
    &-more {
      margin-left: 3px;
    }
  }

  &-inline {
    margin-right: -15px;
    
    li {
      display: inline-block;
      margin-bottom: 15px;
      margin-right: 15px;
      margin-left: 0px;
    }
  }
}

.news-archieve {
  display: none;

  &.opened {
    display: block;
  }
}
.page-header + .news-archieve {
  margin-top: -60px;
}

/* ---
DOCS PAGE --- */
.docs {
  &-group {
    margin-bottom: 70px;

    &-title {
      color: $primary;
      margin-bottom: 48px;
      margin-top: 0;
      @include fz(54);
      line-height: 1.1;
    }
    &-list {
      // display: flex;
      // flex-wrap: wrap;
      // margin-right: rowMargin(2);
      @include createRow(2);
    }

    &-col {
      @include createCols(2, 2) // flex: 0 0 50%;
        // max-width: 50%;
        // padding-right: colPadding(2);
;
    }
  }

  &-card {
    position: relative;
    display: block;
    margin-bottom: 30px;
    min-height: calc(100% - 30px);
    border: 1px solid #fff;
    border-radius: 8px;
    padding: 30px 30px 60px;
    @include fz(22);

    &-title {
      margin-bottom: 24px;
      @include fz(28);
    }
    &-type {
      color: #fff;
      font-weight: 600;
      transition: color 0.3s;
      position: absolute;
      left: 30px;
      bottom: 30px;
    }
    &-icon {
      position: absolute;
      bottom: 30px;
      right: 30px;
      width: 24px;
      height: 24px;
      stroke: $primary;
      stroke-width: 2;
      fill: none;
      opacity: 0;
      transform: scale(0.75);
      transition: opacity 0.3s, transform 0.3s;
    }
    &:hover {
      color: $primary;
      border-color: $primary;

      .docs-card-icon {
        opacity: 1;
        transform: scale(1);
      }
      .docs-card-type {
        color: $primary;
      }
    }

    &-black {
      color: $bgc;
      border-color: $bgc;

      .docs-card {
        &-type {
          color: $bgc;
        }
      }
    }
  }

  @media (max-width: 1259px) {
    &-group {
      &-title {
        @include fz(42);
      }
    }

    &-card {
      @include fz(18);

      &-title {
        @include fz(24);
      }
    }
  }
}

/* ---
CERTS --- */
.certs {
  &-list {
    @include createRow(3);
  }
  &-col {
    @include createCols(3, 3);
  }

  @media (max-width: 1439px) {
    &-list {
      @include createRow(2);
    }
    &-col {
      @include createCols(3, 2);
    }
  }

  @media (max-width: 1259px) {
    &-list {
      margin-right: -30px;
    }
    &-col {
      padding-right: 30px;
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
}

.cert-item {
  margin-bottom: 95px;

  &-img {
    margin-bottom: 21px;
    filter: drop-shadow(4px 4px 30px rgba(0, 0, 0, 0.15));
    position: relative;
    line-height: 0;

    a {
      display: inline-block;
      position: relative;
      overflow: hidden;
    }

    img {
      transition: transform .5s;
      display: block;
      line-height: 0;
    }

    a:hover {
      img {
        transform: scale(1.03);
      }
      
      .cert-item-zoom {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
  &-zoom {
    position: absolute;
    right: 30px;
    bottom: 30px;
    opacity: 0;
    width: 50px;
    height: 50px;
    transform: scale(0.75);
    transition: opacity 0.3s, transform 0.3s;
    stroke: $primary;
    stroke-width: 3;
    fill: none;
  }

  @media (max-width: 1259px) {
    @include fz(14);
    margin-bottom: 75px;

    &-img {
      max-width: 215px;
    }
    &-descr {
      max-width: 360px;
    }
  }
}




