.swiper-rewards {
  margin: 0px -10px;

  &-img {
    height: 310px;
    line-height: 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex: 0 0 33%;
    max-width: 33.3%;
    margin-right: 8%;

    img {
      max-height: 310px;
      transition: transform .5s;
    }
  }

  &-item {
    padding: 10px;

    &:hover {
      .swiper-rewards-img {
        img {
          transform: scale(1.05);
