@each $name, $value in ("blue": $blue, "brown": $brown, "secondary": $secondary, "tertiary": $tertiary) {
  .ui-theme-#{$name} {
    .ui-theme-color {
      color: $value;
    }
    .productions-tabs {
      &-link {
        &:hover,
        &.selected {
          color: $value;
          border-color: $value;
        }
      }
    }
  
    .tk-subtype {
      &-title {
        color: $value;
      }
    }
  
    .productions-management {
      &-name,
      &-contacts-label,
      a:hover {
        color: $value;
      }
    }
  
    .docs-card {
      &-icon {
        stroke: $value;
      }
      &:hover {
        color: $value;
        border-color: $value;
  
        .docs-card-type {
          color: $value;
        }
      }
    }
  
    .ui-btn {
      &:hover {
        color: $value;
        border-color: $value;
  
        .ui-btn-arrow {
          stroke: $value;
        }
      }
    }
  
    .productions-sights-title {
      color: $value;
    }
  }
}

.bbi-parallax {
  position: relative;
  margin-bottom: -80px;

  &-img {
    position: absolute;
    left: 0;
    top: 0;

    &-1 {
      z-index: 3;
    }
    &-2 {
      z-index: 2;
    }

    &-4 {
      position: static;
    }
  }
}

.museum-circle {
  max-width: 170px;

  @media (max-width: 1259px) {
    max-width: 130px;
  }
}